/**
 * define basic styles
 * bqliu | 07/22/2017
 */

@import './vars.less';

html {
  height: 100%;
  box-sizing: border-box;

  body {
    position: relative;
    height: 100%;
    line-height: initial;
  }

  *,
  *:before,
  *:after {
    box-sizing: inherit;;
  }
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    margin: 0;
    padding: 0;
  }
}

img {
  border: none;
  display: inline-block;
}

a,
a:link,
a:visited,
a:hover,
a:active {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5 {
  padding: 0;
  margin: 0;
}

.optimize-scroll {
  -webkit-overflow-scrolling: touch;
}

input[type=text]::-ms-clear {
  display:none
}

.xinput {
  height: 1rem;
  border: none;
  border-bottom: 1px solid @color-middle;
  outline: none;
  padding: 0 0 0 2px;
  border-radius: 0;
  font-size: @font-size-larger;
}

.xbtn {
  outline: none;
  border: none;
  background-color: @color-deep;
  color: #fff;
}

.xbtn-large {
  width: 100%;
  height: 1.06rem;
  border-radius: 1rem;
  font-size: @font-size-larger;
}

.xicon {
  vertical-align: middle;
  display: inline-block;
  background-size: 100% auto;
  background-position: center center;
  background-repeat: no-repeat;
}

.xicon-up {
  width: 0.48rem;
  height: 0.5rem;
  background-image: url(../assets/xicon-up.png);
}

.xicon-up--liked {
  width: 0.48rem;
  height: 0.5rem;
  background-image: url(../assets/xicon-up-liked.png);
}

.xicon-up--b,
.xicon-collect--b {
  width: 0.8rem;
  height: 0.8rem;
}

.xicon-up--b {
  background-image: url(../assets/xicon-up--b.png);
}

.xicon-collect--b {
  background-image: url(../assets/xicon-collect--b.png);
}

.xicon-comment {
  width: 0.5rem;
  height: 0.5rem;
  background-image: url(../assets/xicon-comment.png);
}

.xicon-main {
  width: 0.5rem; // 26px;
  height: 1rem; // 40px;
  background-image: url(../assets/xicon-main.png);
}

.xicon-add {
  width: 0.61rem; // 45px;
  height: 0.61rem; // 45px;
  background-image: url(../assets/xicon-add.png);
}

.xicon-mine {
  width: 0.5rem; // 30px;
  height: 1rem; // 39px
  background-image: url(../assets/xicon-mine.png);
}
.router-link-active {
  .xicon-main {
    background-image: url(../assets/xicon-main-active.png);
  }
  .xicon-add {
    background-image: url(../assets/xicon-add-active.png);
  }
  .xicon-mine {
    background-image: url(../assets/xicon-mine-active.png);
  }
}
.xicon-back {
  width: 0.26rem;
  height: 0.53rem;
  background-image: url(../assets/xicon-back.png);
}

.xicon-back-white {
  width: 0.26rem;
  height: 0.53rem;
  background-image: url(../assets/xicon-back-white.png);
}

.xicon-send {
  width: 0.64rem;
  height: 0.64rem;
  background-image: url(../assets/xicon-send.png);
}



